.today {
  background-color: #91abd7d4;
  width: 40em;
  position: absolute;
  right: 25em;
  border-radius: 15px;
  margin-top: 5px;
}

.today h2, h3 {
  color: #51525f;
}

.todays-title {
  text-align: center;
}

.todays-conditions {
  display: inline-block;
  position: absolute;
  right: 10px;
}

.current-weather {
  display: inline-block;
}


